* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main {
  position: relative;
  width: 1440px;
  height: 960px;
  background: #ffffff;
}
.main-img {
  position: absolute;
  width: 505px;
  height: 330px;
  left: 230px;
  top: 96px;
}
.main-title {
  position: absolute;
  width: 380px;
  height: 50px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 25px - 319px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}
.main-copy {
  position: absolute;
  width: 380px;
  height: 96px;
  left: calc(50% - 190px + 300px);
  top: calc(50% - 48px - 222px);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}
.main-btn {
  position: absolute;
  width: 128px;
  height: 48px;
  left: calc(50% - 190px + 300px);
  top: 338px;
  background: #e4c1ae;
  border-radius: 32px;
  border: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
  color: #ffffff;
}